.quad {
  max-width: 600px;
  margin: 3rem auto;
  position: relative;
  z-index: 0;
}

.quad > img {
  width: 100%;
}

.squares {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 1;

  border-right: 1px solid #bbb;
  border-top: 1px solid #bbb;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-shadow: 0px 7px 45px #999, 0px 3px 0px #ccc;
          box-shadow: 0px 7px 45px #999, 0px 3px 0px #ccc;
  background: white;
  overflow: hidden;
}


